import React from 'react';
import {
  Button,
  List,
  ListItem,
  ListItemText,
  TextField
} from '@mui/material';
import { Link } from 'react-router-dom';
import { RepositoryPayload } from '../types';
import AddModal from './AddModal';
interface Props {
  repos: RepositoryPayload[];

}
const Sidebar: React.FC<Props> = props => {
  const [selectedIndex, setSelectedIndex] = React.useState(0);
  const [open, setOpen] = React.useState(false);
 
  const renderRepos = props.repos.map((item, index)=> <ListItem
    onClick={() => setSelectedIndex(index)}
    selected={selectedIndex === index}
    component={Link}
    to={'/repositories/' + item.id + '/'}
    button
    key={item.id}
  >
    <ListItemText primary={item.repo_name} className='text-nm' />
  </ListItem>)


  return (
    <div className="sidebar">
      <div className='d-flex justify-content-center pt-5'>
      </div>
      <div className='d-flex justify-content-center text-center default-font py-3'>
      </div>
      <div className='d-flex justify-content-between  align-items-center'>
        <b className='text-nm'>
          Recent Repositories
        </b>
        <Button variant="contained" color="success" size="small" onClick={()=>setOpen(true)}>
  Add
</Button>
<AddModal open={open} setOpen={setOpen}/>
      </div>
      <TextField
          id='outlined-required'
          label='Find a repository'
          size="small"
          className='mt-3 w-100 text-nm'
        />
        <div>
          <List
            component='nav'
            aria-label='main mailbox folders'
            
          >
            
           {renderRepos}
          </List>
          
        </div>
      
    </div>
  );
};

export default Sidebar;
