import React from 'react';
import { Link } from 'react-router-dom';
import '../App.css';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import {
  CardActionArea,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  SelectChangeEvent,
} from '@mui/material';
import StorageIcon from '@mui/icons-material/Storage';
import GroupIcon from '@mui/icons-material/Group';
import HowToRegIcon from '@mui/icons-material/HowToReg';
import DashboardSidebar from './DashboardSidebar';
import { Line, Pie } from 'react-chartjs-2';
import ArchiveIcon from '@mui/icons-material/Archive';
import WarningIcon from '@mui/icons-material/Warning';
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
} from 'chart.js';

interface Props {}

const Navbar: React.FC<Props> = (props) => {
  const [age, setAge] = React.useState('week');
  const [count, setCount] = React.useState(2);

  const handleChange = (event: SelectChangeEvent) => {
    setAge(event.target.value as string);
  };

  ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    ArcElement,
    Title,
    Tooltip,
    Legend
  );
  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: 'top' as const,
      },
      title: {
        display: true,
      },
    },
  };

  const labels = [
    'January',
    'February',
    'March',
    'April',
    'May',
    'June',
    'July',
    'August',
  ];

  const data = {
    labels,
    datasets: [
      {
        label: 'Junit5',
        data: [10, 4, 9, 20, 2, 8, 20, 12],
        borderColor: 'rgb(53, 162, 235)',
        backgroundColor: 'rgba(53, 162, 235, 0.5)',
       
      },
      {
        label: 'Pampas',
        data: [2, 4, 9, 10, 4, 3, 0, 2],
        borderColor: 'rgb(255, 99, 132)',
        backgroundColor: 'rgba(255, 99, 132, 0.5)',
       
      },
    ],
  };
  const piedata = {
    labels: ['Pampas', 'Junit5', 'asianoddsgambler-panel'],
    datasets: [
      {
        label: '# of Votes',
        data: [8, 20, 3],
        backgroundColor: [
          'rgba(255, 99, 132, 0.2)',
          'rgba(54, 162, 235, 0.2)',
          'rgba(255, 206, 86, 0.2)',
          'rgba(75, 192, 192, 0.2)',
          'rgba(153, 102, 255, 0.2)',
          'rgba(255, 159, 64, 0.2)',
        ],
        borderColor: [
          'rgba(255, 99, 132, 1)',
          'rgba(54, 162, 235, 1)',
          'rgba(255, 206, 86, 1)',
          'rgba(75, 192, 192, 1)',
          'rgba(153, 102, 255, 1)',
          'rgba(255, 159, 64, 1)',
        ],
        borderWidth: 1,
      },
    ],
  };
  return (
        <div >
          <div className='grid-3'>
            <Card>
              <CardActionArea component={Link}
    to={'/repositories/'}>
                <CardContent>
                  <Typography
                    variant='body2'
                    color='text.secondary'
                    className='d-flex '
                  >
                    Repositories
                  </Typography>
                  <div className='d-flex justify-content-between align-items-baseline'>
                    <div>
                      <Typography gutterBottom variant='h5' component='div'>
                        3
                      </Typography>
                    </div>
                    <div className='icon-circle'>
                      {' '}
                      <StorageIcon />
                    </div>
                  </div>
                </CardContent>
              </CardActionArea>
            </Card>
            <Card>
              <CardActionArea>
                <CardContent>
                  <Typography
                    variant='body2'
                    color='text.secondary'
                    className='d-flex '
                  >
                    Stale Repositories
                  </Typography>
                  <div className='d-flex justify-content-between align-items-baseline'>
                    <div>
                      <Typography gutterBottom variant='h5' component='div'>
                        20
                      </Typography>
                    </div>
                    <div className='icon-circle text-danger'>
                      {' '}
                      <WarningIcon />
                    </div>
                  </div>
                </CardContent>
              </CardActionArea>
            </Card>
            

            <Card>
              <CardActionArea>
                <CardContent>
                  <Typography
                    variant='body2'
                    color='text.secondary'
                    className='d-flex '
                  >
                    Archived Repositories
                  </Typography>
                  <div className='d-flex justify-content-between align-items-baseline'>
                    <div>
                      <Typography gutterBottom variant='h5' component='div'>
                        2
                      </Typography>
                    </div>
                    <div className='icon-circle'>
                      {' '}
                      <ArchiveIcon />
                    </div>
                  </div>
                </CardContent>
              </CardActionArea>
            </Card>
          </div>
          <div className='grid-bt-2'>
            <div />
            <Card>
              <CardActionArea component={Link}
    to={'/developers/'}>
                <CardContent>
                  <Typography
                    variant='body2'
                    color='text.secondary'
                    className='d-flex '
                  >
                    Developers
                  </Typography>
                  <div className='d-flex justify-content-between align-items-baseline'>
                    <div>
                      <Typography gutterBottom variant='h5' component='div'>
                        30
                      </Typography>
                    </div>
                    <div className='icon-circle'>
                      {' '}
                      <GroupIcon />
                    </div>
                  </div>
                </CardContent>
              </CardActionArea>
            </Card>
            
            <Card>
              <CardActionArea>
                <CardContent>
                  <Typography
                    variant='body2'
                    color='text.secondary'
                    className='d-flex '
                  >
                    Inactive Developers
                  </Typography>
                  <div className='d-flex justify-content-between align-items-baseline'>
                    <div>
                      <Typography gutterBottom variant='h5' component='div'>
                        20
                      </Typography>
                    </div>
                    <div className='icon-circle text-danger'>
                      {' '}
                      <GroupIcon/>
                    </div>
                  </div>
                </CardContent>
              </CardActionArea>
            </Card>
          </div>

          {/* <div className='grid-2'>
          <div>
            <Card>Most Active repository of past week</Card>
          </div>
          <div>
            <Card>Least Active repository of past week</Card>
          </div>
        </div>
        <div>Other repositories</div> 
        <div>archived repositories</div>
        
        */}
         <div className='pt-4'>
           <FormControl size="small">
                  <InputLabel id='demo-simple-select-label'>Time</InputLabel>
                  <Select
                    labelId='demo-simple-select-label'
                    id='demo-simple-select'
                    value={age}
                    label='time'
                    onChange={handleChange}
                  >
                    <MenuItem value='week'>Weekly</MenuItem>
                    <MenuItem value='month'>Monthly</MenuItem>
                    <MenuItem value='year'>Yearly</MenuItem>
                  </Select>
                </FormControl></div>
          <div className='d-flex'>
            {/* <div>Hello wirkjsfbv;dskjbfv;ajfn aljdv;asvn ;lkadf;sain ;ldjn;sld ;ldjnv;s</div> */}
            
            <div className='flex-grow-1'>
              <h4 className='pt-1 text-start'>Top Repositories</h4>
              <div className='d-flex'>
               
                <FormControl size="small">
                  <InputLabel id='demo-simple-select-label'></InputLabel>
                  <Select
                    labelId='demo-simple-select-label'
                    id='demo-simple-select'
                    value={count.toString()}
                    label='count'
                    // onChange={handleChange}
                  >
                    <MenuItem value={2}>2</MenuItem>
                    <MenuItem value={3}>3</MenuItem>
                    <MenuItem value={4}>4</MenuItem>
                    <MenuItem value={5}>5</MenuItem>
                  </Select>
                </FormControl>
              </div>
              <Line options={options} data={data} />
            </div>
            <div>
              <h4 className='pt-1 text-start'>Repositories Contribution</h4>
             <div className='h-100 d-flex align-items-center'> <Pie data={piedata} /></div>
            </div>
          </div>
        </div>
   
  );
};

export default Navbar;
