export const LoginEpic = combineEpics(
  fetchRepositoriesEpic,
  fetchBranchesEpic,
  fetchGraphdataEpic,
  fetchEpic,
  addRepoEpic,
  fetchCommitsEpic,
  fetchDevelopersEpic,
  fetchDevelopersGraphdataEpic,
  fetchDevCommitsEpic
);

export default LoginEpic;
